EAClD;;EAEA;IACE,gDAAgD;EAClD;AACF;;AAEA,WAAW;AACX;EACE;IACE,qBAAqB;EACvB;;EAEA;IACE,gBAAgB;IAChB,cAAc;IACd,6BAA6B;IAC7B,0BAA0B;IAC1B,WAAW;IACX,YAAY;IACZ,aAAa;IACb,UAAU;EACZ;;EAEA;IACE,qBAAqB;EACvB;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,aAAa;IACb,gBAAgB;IAChB,gBAAgB;IAChB,MAAM;IACN,QAAQ;IACR,WAAW;IACX,YAAY;IACZ,YAAY;IACZ,UAAU;IACV,mBAAmB;EACrB;;EAEA;IACE,eAAe;IACf,yBAAyB;EAC3B;;EAEA,iBAAiB;EACjB;IACE,8BAA8B;EAChC;;EAEA;IACE,WAAW;IACX,0BAA0B;EAC5B;;EAEA;IACE,iBAAiB;IACjB,uBAAuB;IACvB,WAAW;IACX,YAAY;EACd;;EAEA,WAAW;EACX;IACE,aAAa;EACf;;EAEA;IACE,mBAAmB;IACnB,kBAAkB;EACpB;;EAEA;IACE,gDAAgD;EAClD;;EAEA;IACE,UAAU;EACZ;;EAEA;IACE,gDAAgD;IAChD,WAAW;EACb;;EAEA;IACE,gDAAgD;EAClD;;EAEA;IACE,UAAU;EACZ;AACF;;AAEA,UAAU;AACV;EACE,aAAa;EACb;IACE,eAAe;EACjB;;EAEA;IACE,gDAAgD;EAClD;;EAEA;IACE,gDAAgD;EAClD;AACF;;AAEA,gBAAgB;AAChB;AACA","sourcesContent":["/* Google Font */\n@import url(\"https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800&display=swap\");\n\n*,\n::before,\n::after {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\n:root {\n  --color-dark-blue: #0f172a; /* Warna biru sangat gelap */\n  --color-light-gray: #dbdbdb; /* Abu-abu terang */\n  --color-gray: #868686; /* Abu-abu */\n  --color-translucent-white: #ffffff99; /* Putih semi transparan */\n  --color-soft-gray: #f3f3f3; /* Abu-abu lembut */\n  --color-medium-gray: #c7c7c7; /* Abu-abu sedang */\n\n  --color-black: #000000; /* Hitam */\n  --color-white: #ffffff; /* Putih */\n  --color-red: #ff0000; /* Merah */\n}\n\nhtml {\n  scroll-behavior: smooth;\n}\n\nbody {\n  font-family: \"Poppins\", serif;\n}\n\na {\n  text-decoration: none;\n  color: black;\n}\n\n/* Skip to Content */\n.skip-to-content {\n  position: absolute;\n  top: -45px;\n  left: 10px;\n  background: var(--color-black);\n  color: var(--color-white);\n  padding: 8px 16px;\n  z-index: 100;\n  transition: top 0.3s;\n  text-decoration: none;\n}\n\n.skip-to-content:focus {\n  top: 10px;\n}\n\n/* Loader */\n/* HTML: <div class=\"loader\"></div> */\n.loader-wrapper {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  height: 50vh;\n}\n\n.loader {\n  width: 50px;\n  aspect-ratio: 1;\n  --c: no-repeat radial-gradient(farthest-side, #514b82 92%, #0000);\n  background: var(--c) 50% 0, var(--c) 50% 100%, var(--c) 100% 50%,\n    var(--c) 0 50%;\n  background-size: 10px 10px;\n  animation: l18 1s infinite;\n  position: relative;\n}\n.loader::before {\n  content: \"\";\n  position: absolute;\n  inset: 0;\n  margin: 3px;\n  background: repeating-conic-gradient(#0000 0 35deg, #514b82 0 90deg);\n  mask: radial-gradient(farthest-side, #0000 calc(100% - 3px), #000 0);\n  border-radius: 50%;\n}\n@keyframes l18 {\n  100% {\n    transform: rotate(0.5turn);\n  }\n}\n\n/* Classes */\n.container {\n  margin: 0 auto;\n  padding-inline: 3rem;\n}\n\n.text-center {\n  text-align: center;\n}\n\n.flex {\n  display: flex;\n}\n\n.justify-between {\n  justify-content: space-between;\n}\n\n.justify-center {\n  justify-content: center;\n}\n\n.items-center {\n  align-items: center;\n}\n\n.mt-4 {\n  margin-top: 1rem;\n}\n\n.mt-2 {\n  margin-top: 0.5rem;\n}\n\n.mb-2 {\n  margin-bottom: 0.5rem;\n}\n\n.p-4 {\n  padding: 1rem;\n}\n\n.pb-4 {\n  padding-bottom: 1rem;\n}\n\n.text-justify {\n  text-align: justify;\n}\n\n.text-white {\n  color: var(--color-white);\n}\n\n.text-red {\n  color: var(--color-red);\n}\n\n.object-cover {\n  object-fit: cover;\n}\n\n.object-contain {\n  object-fit: contain;\n}\n\n.hidden {\n  display: none;\n}\n\n/* Header */\nheader {\n  padding-block: 0.8rem;\n  border-bottom: 1px solid var(--color-light-gray);\n  font-size: 1.1rem;\n  font-style: normal;\n  font-weight: 400;\n}\n\n.nav {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n}\n\n.nav-list {\n  position: fixed;\n  inset: 0;\n  background-color: rgba(0, 0, 0, 0.452);\n  backdrop-filter: blur(15px);\n  width: 100%;\n  height: 100%;\n  z-index: -1;\n  opacity: 0;\n  transition: all 0.5s;\n}\n\n.nav-menu {\n  height: 100%;\n  list-style-type: none;\n  background-color: var(--color-white);\n  position: absolute;\n  top: 0;\n  right: -100%;\n  width: 50%;\n  padding: 1.5rem 2rem;\n  transition: all 0.7s ease;\n  line-height: 2rem;\n}\n\n.brand,\n.nav-link a {\n  padding: 0.7rem 0.5rem;\n}\n\n.nav-link:hover {\n  padding-left: 0.5rem;\n  transition: all 0.3s ease;\n}\n\n.nav-list.active {\n  z-index: 10;\n  opacity: 1;\n}\n\n.nav-menu.active {\n  right: 0;\n}\n\n.hamburger-menu {\n  border: none;\n  background-color: transparent;\n  font-size: 1.8rem;\n  cursor: pointer;\n  padding: 0.6rem;\n}\n\n/* Hero Section */\nmain section.hero-section {\n  min-height: auto;\n}\n\nsection.hero-section figure img {\n  width: 100%;\n}\n\n/* restoran Section */\nmain section#restoran {\n  padding-top: 1.5rem;\n  padding-bottom: 2rem;\n}\n\nmain section#restoran h1 {\n  margin-bottom: 1rem;\n}\n\n.title-page {\n  text-align: center;\n  margin-bottom: 0.75rem;\n}\n\n.card-container {\n  display: grid;\n  grid-template-columns: minmax(0, 1fr);\n  gap: 10px;\n  margin-bottom: 2rem;\n}\n\n.card {\n  border: 1px solid var(--color-light-gray);\n  border-radius: 1rem;\n  position: relative;\n  overflow: hidden;\n}\n\n.card-body {\n  padding: 0 1rem 1rem;\n}\n\n.card figure {\n  min-width: 100%;\n  width: 300px;\n  height: 250px;\n  overflow: hidden;\n}\n\n.card figure:hover img {\n  transform: scale(1.1);\n}\n\n.card figure img {\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n  object-position: center;\n  transition: all 0.3s;\n}\n\n.card .rating {\n  font-size: 0.9rem;\n  color: var(--color-dark-blue);\n}\n\n.card .title {\n  font-size: 1.3rem;\n  padding-block: 0.5rem;\n  font-weight: bold;\n  display: block;\n}\n\n.card .description {\n  text-align: justify;\n  display: -webkit-box;\n  -webkit-box-orient: vertical;\n  overflow: hidden;\n  -webkit-line-clamp: 4;\n  line-clamp: 4;\n}\n\n.card .kota {\n  position: absolute;\n  top: 0rem;\n  left: 0rem;\n  background-color: rgba(255, 255, 255, 0.596);\n  backdrop-filter: blur(5px);\n  padding: 0.5rem 1rem;\n  border-bottom-right-radius: 0.3rem;\n  z-index: 1;\n}\n\n/* Section Detail */\n.title-detail {\n  margin-block: 1rem;\n  font-size: 2.4rem;\n}\n\nsection#detail figure {\n  cursor: pointer;\n  margin-bottom: 1rem;\n  height: 250px;\n  overflow: hidden;\n  background-color: var(--color-soft-gray);\n  border-radius: 8px;\n}\n\nsection#detail figure img {\n  height: 100%;\n  width: 100%;\n  object-position: center;\n}\n\nbutton.favorite {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  cursor: pointer;\n  border: none;\n  position: fixed;\n  right: 1.3rem;\n  bottom: 1.3rem;\n  font-size: 2rem;\n  background-color: var(--color-dark-blue);\n  border-radius: 50%;\n  width: 3.5rem;\n  height: 3.5rem;\n}\n\n.detail-menu {\n  display: flex;\n  row-gap: 0.5rem;\n  flex-direction: column;\n  margin-top: 0.5rem;\n}\n\n.menu-container {\n  width: 100%;\n}\n\n.menu {\n  border-radius: 8px;\n  width: 100%;\n  display: grid;\n  gap: 0.5rem;\n  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));\n}\n\n.menu-item {\n  background-color: var(--color-soft-gray);\n  color: var(--color-black);\n  padding: 0.5rem;\n  border-radius: 5px;\n  font-size: 14px;\n}\n\n.review-container {\n  display: grid;\n  gap: 1rem;\n  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));\n}\n\n.review-customer {\n  margin: 0;\n  border: 1px solid var(--color-medium-gray);\n  padding: 0.5rem;\n  border-radius: 5px;\n}\n\n.review-customer .date-review {\n  font-size: 0.8rem;\n  color: var(--color-gray);\n}\n\n.form-review {\n  width: 100%;\n}\n\n.form-review label {\n  display: block;\n  font-weight: bold;\n}\n\n.form-review label::after {\n  content: \"*\";\n  color: var(--color-red);\n  padding-left: 3px;\n}\n\n.form-review input[name=\"name\"] {\n  font-family: inherit;\n  width: 100%;\n  padding: 0.75rem 0.5rem;\n  border-radius: 5px;\n  outline: none;\n  border: 1px solid var(--color-medium-gray);\n}\n\n.form-review textarea {\n  font-family: inherit;\n  width: 100%;\n  resize: vertical;\n  min-height: 130px;\n  padding: 0.75rem 0.5rem;\n  border-radius: 5px;\n  outline: none;\n  border: 1px solid var(--color-medium-gray);\n}\n\n.form-review button {\n  border: none;\n  background-color: var(--color-dark-blue);\n  color: var(--color-white);\n  width: 100%;\n  text-align: center;\n  padding: 0.75rem 0.5rem;\n  font-size: 1.2rem;\n  border-radius: 5px;\n  font-weight: bold;\n  cursor: pointer;\n}\n\n.form-review input[name=\"name\"]:focus,\n.form-review textarea:focus {\n  transition: all 0.3s;\n  border: 1px solid var(--color-dark-blue);\n}\n\n/* Favorite section */\nsection#favorite-resto {\n  min-height: 100vh;\n}\n\nsection#favorite-resto p.empty-restaurant {\n  color: var(--color-red);\n  text-align: center;\n}\n\n/* Footer */\nfooter {\n  border-top: 1px solid var(--color-light-gray);\n  text-align: center;\n  padding-block: 1.5rem;\n  height: 100px;\n}\n\nfooter p {\n  margin-block: 0.5rem;\n}\n\n/* Media Query - Responsive */\n/* Small */\n@media screen and (min-width: 640px) {\n  .card-container {\n    grid-template-columns: repeat(2, minmax(0, 1fr));\n  }\n\n  .review-container {\n    grid-template-columns: repeat(2, minmax(0, 1fr));\n  }\n}\n\n/* Medium */\n@media screen and (min-width: 768px) {\n  header {\n    padding-block: 0.8rem;\n  }\n\n  .nav-list {\n    position: static;\n    inset: initial;\n    background-color: transparent;\n    backdrop-filter: blur(0px);\n    width: auto;\n    height: auto;\n    z-index: auto;\n    opacity: 1;\n  }\n\n  .brand {\n    display: inline-block;\n  }\n\n  .hamburger-menu {\n    display: none;\n  }\n\n  .nav-menu {\n    display: flex;\n    column-gap: 2rem;\n    position: static;\n    top: 0;\n    right: 0;\n    width: auto;\n    height: auto;\n    padding: 0px;\n    z-index: 1;\n    line-height: normal;\n  }\n\n  .nav-link:hover {\n    padding-left: 0;\n    transition: all 0.3s ease;\n  }\n\n  /* Hero Section */\n  section.hero-section {\n    min-height: calc(100vh - 74px);\n  }\n\n  section.hero-section figure {\n    width: 100%;\n    height: calc(100vh - 74px);\n  }\n\n  section.hero-section figure img {\n    object-fit: cover;\n    object-position: center;\n    width: 100%;\n    height: 100%;\n  }\n\n  /* Detail */\n  section#detail figure {\n    height: 400px;\n  }\n\n  .detail-menu {\n    flex-direction: row;\n    column-gap: 1.7rem;\n  }\n\n  .menu {\n    grid-template-columns: repeat(3, minmax(0, 1fr));\n  }\n\n  .menu-container {\n    width: 50%;\n  }\n\n  .card-container {\n    grid-template-columns: repeat(3, minmax(0, 1fr));\n    gap: 1.5rem;\n  }\n\n  .review-container {\n    grid-template-columns: repeat(3, minmax(0, 1fr));\n  }\n\n  .form-review {\n    width: 50%;\n  }\n}\n\n/* Large */\n@media screen and (min-width: 1024px) {\n  /* restoran */\n  .title {\n    font-size: 2rem;\n  }\n\n  .menu {\n    grid-template-columns: repeat(4, minmax(0, 1fr));\n  }\n\n  .review-container {\n    grid-template-columns: repeat(4, minmax(0, 1fr));\n  }\n}\n\n/* Extra Large */\n@media screen and (min-width: 1280px) {\n}\n"],"sourceRoot":""}]);
